.Timeline
{
    background-color: #393A3D;
    background-image: linear-gradient(to right, #393A3D 0%, #4a4b50 50%, #393A3D 100%);
    width           : 100%;
    display         : flex;
    flex-direction  : column;
}

.Timeline .Slot
{
    flex           : 1;
    display        : flex;
    flex-direction : row;
    align-content  : center;
    justify-content: center;
}
