.Project-Brief
{
    font-family   :'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size     : large;
    color         : #c0c0c0;
    display       : flex;
    flex-direction: column;
    min-width     : 500px;
    width         : 70%;
}

.Project-Left
{
    text-align: left;
}

.Project-Right
{
    text-align: right;
}

.Project-Title
{
    font-family   : 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size     : xx-large;
    font-weight   : bold;
    color         : #C9C9C9;
    margin        : 0;
    text-transform: uppercase;
}

.Project-Block
{
    flex   : 1;
    padding: 15px;
}

.Project-Description
{
    border-top   : solid #c0c0c0;
    border-bottom: solid #c0c0c0;
    font-weight  : lighter;
    padding      : 10px 0px;
    margin       : 10px 0px;
}

/* -----------------------------------------------------------------------------
Links
----------------------------------------------------------------------------- */

.Project-Link
{
    font-family   :'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display       : flex;
    flex-direction: row;
    align-content : center;
}

.Project-Link span
{
    display       : flex;
    flex-direction: row;
    margin        : 0px 10px;
}

.Project-Link a
{
    color          : silver;
    font-weight    : lighter;
    text-decoration: none;
    height         : 45px;
    margin         : 0px 10px;
}

/* -----------------------------------------------------------------------------
Hashtags
----------------------------------------------------------------------------- */

.Project-Hashtag
{
    display       : flex;
    flex-direction: row;
    padding       : 10px 0px;
}

.Project-Hashtag span
{
    border-radius: 5px;
    color        : #393A3D;
    font-family  : 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size    : smaller;
    padding      : 5px 10px;
    margin       : 0px 10px 0px 0px;
}

.Project-Hashtag .programming
{
    background-color: #2e8b57;
}

.Project-Hashtag .leadership
{
    background-color: #daa520;
}

.Project-Hashtag .design
{
    background-color: #87ceeb;
}
