.Contacts
{
    color           : #8892a5;
    background-color: #393e47;
    width           : fit-content;
    padding         : 0px 30px;
    max-height      : 0px;
    transition      : max-height 0.2s ease-out;
    margin          : 0;
}

.Contact
{
    text-decoration: none;
    font-weight    : lighter;
    position       : relative;
    padding        : 5px;
    display        : flex;
    flex-direction : row;
    align-items    : center;
    justify-content: left;
}

.Contacts img
{
    width        : auto;
    height       : 30px;
    padding-right: 10px;
}
