.ContactMe
{
    position: fixed;
    bottom  : 0;
    left    : 20px;
}

.ContactMe .Tab
{
    width           : 200px;
    border-radius   : 10px 10px 0px 0px;
    border-top      : #B59959 solid;
    background-color: #393E47;
    color           : silver;
    text-align      : center;
    padding         : 10px;
    font-weight     : bolder;
    text-transform  : uppercase;
    font-family     : 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
